.image {
  width: 22%;
  height: 200px;
  border-radius: 4px;
  overflow: hidden;
  margin: 10px;

  img {
    width: 100%;
    height: 100%;

    object-fit: cover;
    transition: transform 0.2s ease;

    &:hover {
      transform: scale(1.2);
      cursor: pointer;
    }
  }
}
